  },
  created() {
    var date = new Date();
    var seperator1 = '/';
    var seperator2 = ':';
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = '0' + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = '0' + strDate;
    }
    var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate +
            ' ' + '00' + seperator2 + '00' +
            seperator2 + '00';
    // console.log(currentdate)
    this.queryFilter.start = new Date(currentdate)

