  methods: {

    getreporttypelistall() {
      this.getreporttypelist(this.cid, this.pids)
    },
    getNowFormatDate() {
      var date = new Date();
      var seperator1 = '-';
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = '0' + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate;
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate;
      this.newdate = currentdate;
    },
    async getreporttypelist1(cid, pids) {
      const params = {
        cid,
        pids
      }
      try {
        const { data } = await reporttypelist(params)
        this.cid = cid
        this.reporttype = data.types
        // this.equipmentlistz = []
        const equipmentlistz = []
        for (let i = 0; i < this.equipmentlist.length; i++) {
          for (let j = 0; j < this.reporttype.length; j++) {
            if (this.equipmentlist[i].code == this.reporttype[j]) {
              equipmentlistz.push(this.equipmentlist[i])
            }
          }
        }
        this.equipmentlistz = equipmentlistz
      } catch (error) {
        msgUtil.error('查询数据失败', msgUtil.findErrorMsg(error))
      }
    },
    async getreporttypelist(cid, pids) {
      reporttypelist({ cid: cid, pids: pids }).then(resp => {
        if (resp.status == 200) {
          this.reporttype = resp.data.types
          // console.log(this.reporttype)
          this.equipmentlistz = []
          for (let i = 0; i < this.equipmentlist.length; i++) {
            for (var j = 0; j < this.reporttype.length; j++) {
              if (this.equipmentlist[i].code == this.reporttype[j]) {
                this.equipmentlistz.push(this.equipmentlist[i])
              }
            }
          }
          this.device_type = ''
          for (let i = 0; i < this.equipmentlistz.length; i++) {
            if (this.equipmentlistz[i].checked) {
              this.device_type = this.device_type + this.equipmentlistz[i].code + ','
            }
          }
          console.log(this.device_type)
          this.generate()
        }
      }).catch(e => {
        msgUtil.error('请求设备列表失败')
      })
    },
    async getagentlist(cid) {
      try {
        const { data } = await agentlist(cid)
        if (data.level == 'province') {
          if (data.companys_list.length === 0) return
          this.provinceagentlist = data.companys_list
          for (let i = 0; i < this.provinceagentlist.length; i++) {
            if (this.provinceagentlist[i].type_level == 'CH') {
              this.provinceagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'city') {
          if (data.companys_list.length === 0) return
          this.cityagentlist = data.companys_list
          for (let i = 0; i < this.cityagentlist.length; i++) {
            if (this.cityagentlist[i].type_level == 'CH') {
              this.cityagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'county') {
          if (data.companys_list.length === 0) return
          this.countyagentlist = data.companys_list
          for (let i = 0; i < this.countyagentlist.length; i++) {
            if (this.countyagentlist[i].type_level == 'CH') {
              this.countyagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'village') {
          // this.villageagentlist = data.companys_list
        }
      } catch (error) {
        msgUtil.error('查询数据失败', msgUtil.findErrorMsg(error))
      }
    },
    async getplaceslist(code) {
      try {

        const { data } = await placeslist(code)
        this.placeslist = data
      } catch (error) {
        msgUtil.error('查询数据失败', msgUtil.findErrorMsg(error))
      }
    },
    deleteChecked(item) {
      item.checked = !item.checked
      this.equipmentlistz
    },
    choseequipment() {
      this.showequipment = !this.showequipment
    },
    changecheck(item) {
      item.checked = !item.checked
    },
    initData() {

    },
    allchangechecked() {
      for (let i = 0; i < this.equipmentlistz.length; i++) {
        this.equipmentlistz[i].checked = true
      }
      this.allchecked = true
      this.dischecked = false
    },
    dischangechecked() {
      for (let i = 0; i < this.equipmentlistz.length; i++) {
        this.equipmentlistz[i].checked = false
      }
      this.dischecked = true
      this.allchecked = false
    },
    allcancel() {
      this.showequipment = !this.showequipment
    },
    generate() {
      if (this.queryFilter.start && this.queryFilter.end && this.queryFilter.summarymode && this.cid) {
        if (this.device_type) {
          // console.log(this.device_type)
          reportlist({
            start: Math.ceil(this.queryFilter.start.getTime() / 1000),
            end: Math.ceil(this.queryFilter.end.getTime() / 1000),
            page: this.pageIndex,
            per_page: this.pageSize,
            collect_type: this.queryFilter.summarymode,
            cid: this.cid,
            pids: this.pids,
            device_type: this.device_type.substring(0, this.device_type.lastIndexOf(','))
            // device_type :this.device_type
          }).then(resp => {
            if (resp.status == 200) {
              // msgUtil.success('生成报表成功！')
              this.collect_type = resp.data.collect_type
              this.devices_list = resp.data.devices_list
              console.log(this.devices_list)
              this.pageIndex = resp.data.page_info.page
              this.pageSize = resp.data.page_info.per_page
              this.totalElementNum = resp.data.page_info.total
              this.totalPages = Math.ceil(this.totalElementNum / this.pageSize)
              this.initPages()
              // layer.close(loading)
              countlist({
                start: Math.ceil(this.queryFilter.start.getTime() / 1000),
                end: Math.ceil(this.queryFilter.end.getTime() / 1000),
                // page:this.pageIndex,
                // per_page:this.pageSize,
                collect_type: this.queryFilter.summarymode,
                cid: this.cid,
                pids: this.pids,
                device_type: this.device_type.substring(0, this.device_type.lastIndexOf(','))
                // device_type :this.device_type
              }).then(res => {
                if (res.status == 200) {
                  this.device_typesz = resp.data.device_type
                  this.devices_summary = res.data.devices_summary
                  this.devices_summary.company = res.data.company
                  this.summary_list = res.data.summary_list
                  this.totlelist = 0
                  for (var i = 0; i < this.summary_list.length; i++) {
                    this.totlelist = this.totlelist + this.summary_list[i].number
                  }
                }
              })
            }
          })
        } else {
          msgUtil.error('无安装记录')
        }
      } else {
        msgUtil.error('请填写报表参数')
      }

    },
    initPages() {
      const result = []
      if (this.totalElementNum === 0) {
        result.push(1)
        this.pages = result
        return result
      }
      if (this.totalPages <= 7) {
        for (let i = 1; i <= this.totalPages; i++) {
          result.push(i)
        }
      } else {
        if (this.pageIndex <= 4) {
          result.push(1, 2, 3, 4, 5, 6, 7)
        } else {
          result.push(this.pageIndex - 3, this.pageIndex - 2, this.pageIndex - 1, this.pageIndex)
          const afterLen = this.totalPages - this.pageIndex
          afterLen >= 1 && result.push(this.pageIndex + 1)
          afterLen > 1 && result.push(this.pageIndex + 2)
          afterLen > 2 && result.push(this.pageIndex + 3)
        }
      }
      this.pages = result
    },
    exportPDF() {
      this.generatePDF(true)
    },
    generatePDF(updateData) {
      this.$html2canvas(document.getElementById('projectreport'), { useCORS: true }).then((canvas) => {
        var contentWidth = canvas.width;
        var contentHeight = canvas.height;

        // 一页pdf显示html页面生成的canvas高度;
        var pageHeight = contentWidth / 592.28 * 841.89;
        // 未生成pdf的html页面高度
        var leftHeight = contentHeight;
        // 页面偏移
        var position = 0;
        // a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高
        var imgWidth = 595.28;
        var imgHeight = 592.28 / contentWidth * contentHeight;

        var pageData = canvas.toDataURL('image/jpeg', 1.0);

        var pdf = new jsPDF('', 'pt', 'a4');

        // 有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
        // 当内容未超过pdf一页显示的范围，无需分页
        if (leftHeight < pageHeight) {
          pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight);
        } else {
          while (leftHeight > 0) {
            pdf.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
            leftHeight -= pageHeight;
            position -= 841.89;
            // 避免添加空白页
            if (leftHeight > 0) {
              pdf.addPage();
            }
          }
        }
        pdf.save('安装报表.pdf')
      })
    }
  }
}
